import React from 'react';
import './Popup.scss';
import { useNavigate } from 'react-router-dom';

const Popup = ({ close }) => {
  const navigate = useNavigate();

  const moveToAll = () => {
    navigate('/');
  };

  const moveToBasket = () => {
    navigate('/basket');
  };

  return (
    <div className="popup">
      <div className="delete" onClick={close}>
        X
      </div>
      <div className="explanation">
          .
        <br />
        <br />
          ?
      </div>
      <div className="choice">
        <button onClick={moveToAll}> .</button>
        <button onClick={moveToBasket}> .</button>
      </div>
    </div>
  );
};

export default Popup;
