import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Input from '../../../components/Input/Input';
import Phone from '../../Join/Phone/Phone';
import './FindId.scss';

const FindId = () => {
  const navigate = useNavigate();

  const [userInfo, setUserInfo] = useState({
    name: '',
    phonenumber: '',
  });

  const { name, phonenumber } = userInfo;

  const handleUserInfo = (e) => {
    const { name, value } = e.target;
    setUserInfo({ ...userInfo, [name]: value });
  };

  const findId = (e) => {
    e.preventDefault();
    fetch(
      `http://10.58.52.82:8000/users/find-id?name=${name}&phonenumber=${phonenumber}`,
      {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      },
    )
      .then((response) => response.json())
      .then((data) => {
        console.log('data', data);
        if (data) {
          alert(` ${data.userId} .`);
        }
      });
  };
  return (
    <form className="FindForm" onChange={handleUserInfo} onSubmit={findId}>
      <fieldset className="findBox">
        <legend className="findInfo"> </legend>
        <div className="findInput">
          <p></p>
          <Input
            id="userName"
            type="text"
            name="userName"
            value={name}
            placeholder="  "
          />
          <p></p>
          <Phone />
        </div>
        <div className="confirmButtonBox">
          <button className="confirmButton"></button>
        </div>
      </fieldset>
    </form>
  );
};

export default FindId;
